.root {
  color: #000000;
  display: flex;
  padding: 2px;
  border: 1px solid #727272;
  border-radius: 5px;
  margin-bottom: 1.5rem;
  min-width: 14.6rem;
}

.inp {
  background: transparent;
  padding: 5px 7px;
  font-size: 1rem;
  color: inherit;
  border: 1px solid transparent;
  border-radius: inherit;
}

.inp::placeholder {
  color: #000000;
}

.inp:focus {
  box-shadow: 0 0 3px 0 #ffffff;
  border-color: #fff;
  outline: none;
  color: #000000;
}

.btn {
  margin-right: 0.3rem;
  background: transparent no-repeat center
    url('../../assets/image/icon/icon-search.svg');
  background-size: 1.4rem;
  width: 1.4rem;
}

.btn:focus {
  box-shadow: 0 0 3px 0 #ffffff;
  border-color: #fff;
  outline: none;
  color: #000000;
}

@media (max-width: 768px) {
  .root {
    margin-bottom: 1rem;
  }

  .btn {
    width: 40px;
    background-size: clamp(1.2rem, 2vw, 2.5rem);
  }
}
