:root{box-sizing:border-box;background-color:var(--bg-color-body);overflow-y:hidden;overflow-x:hidden;--base-font: "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;--font-size: clamp(16px, 1vw, 22px);--max-width: 1100px;--main-color: #9bc53d;--bg-color-header: #ffd586;--bg-color-add-to-cart: #ffa544;--bg-color-body: #fefefe;--secondary-color: #ebfffa;--btn-send-com: hsla(196, 83%, 84%, 1);--form-inp-email: #ffffff;--form-border-color: #ffffff}.container{display:flex;flex-direction:column;overflow:hidden;min-height:100%;max-width:var(--max-width);margin:0 auto;padding:0 .6rem;background-color:var(--bg-color-body)}.btn{border:solid 2px var(--btn-send-com);border-radius:.4rem;padding:clamp(.4rem,1vw,1rem);transition:all .25s ease-in-out}.btn:hover{background-color:var(--btn-send-com);box-shadow:-3px -3px 3px #888;transform:translate(2px, 2px)}.btn:active{background-color:var(--btn-send-com);box-shadow:none;transform:translate(-2px, -2px)}button.cart-btn{background:rgba(0,0,0,0) no-repeat center url("../assets/image/icon/shopping-cart.svg");height:34px;padding:.6rem;border-radius:5px;text-indent:-999px;overflow:hidden;width:40px;background-size:1.5rem;cursor:pointer;opacity:.5;transition:opacity .3s ease-in-out}button.cart-btn:hover{opacity:1}.add-to-cart span{background-color:var(--bg-color-add-to-cart);border-radius:50%;margin-left:5px;padding:3px 4px}.clear-cart:hover{background-color:#ff9090;box-shadow:-3px -3px 3px #888;transform:translate(2px, 2px)}.clear-cart:active{background-color:#ff9090;box-shadow:none;transform:translate(-2px, -2px)}.img-wrp{position:relative;top:0;left:0;max-width:30rem;width:100%}.img{width:100%;height:100%;object-fit:cover}.icon-wrp{position:relative;top:0;left:0;max-width:2rem;width:100%}.rating::after{content:"✭";color:#fcc90f;margin-left:4px}